import React from 'react'
function filterContents(props) { 
    let filterKey = props.filterKey;   
    let masterData = props.myContent.value || [];  
    let uniqueArray = removeDuplicates(masterData,filterKey);
    return (  
        <div key={"extraDiv"+filterKey}>          
        {uniqueArray.map((uniqueFilters,index) => (
                 <div className="ms-CheckBox" key={"ms-CheckBox_"+filterKey+"_"+index}>
                    <input key={"filterChk"+index} type="checkbox" className="ms-CheckBox-input"/>
                    <label id={(filterKey || "")+"_"+index} key={"filterlbl"+index} className="ms-CheckBox-field "  
                    name="checkboxa" onClick={(e) => onclickFilters(uniqueFilters, e, (filterKey || "")+"_"+index)}>
                        <span className="ms-Label" key={"filterSpan"+index}>{uniqueFilters}</span> 
                    </label>
                </div> 
        ))}
        </div>
    );
}
function removeDuplicates(masterData,filterKey)
{
    let rawArray = [];
    masterData.map((masterFilters) => (
        masterFilters[filterKey].map((subFilters,index) => (
            rawArray.push(subFilters)
        ))
    ));
    const uniqueSet = new Set(rawArray);
    return Array.from(uniqueSet) || [];
}
function onclickFilters(subFilters, event, element){
    var currentElement = document.getElementById(element);
    var activeClass = "is-checked";
    currentElement.classList.contains(activeClass) ? 
                currentElement.classList.remove(activeClass) : currentElement.classList.add(activeClass);
    alert(subFilters);
}

export default filterContents;