import React from 'react'
class Search extends React.Component {
    static onSearchButtonClick = function(e) {
        let inputText = e.target.offsetParent.children.item(0) ||
                        e.target.offsetParent.children.item[0]
        if((inputText.value||"").replace(/\s/g, '')==="")
        {
            alert("Enter valid Text to search");
        }
        else
        {
            alert(inputText.value);
        }
    };
}
export default Search;